import { Notyf } from 'notyf';
import 'notyf/notyf.min.css';

const notyf = new Notyf({
  duration: 5000,
  types: [
    {
      type: 'error',
      backgroundColor: 'grey',
      message: ' i i.   !'
    }
  ]
});

const createPlanningWeek = () => {
  notyf.success('  !');
};

const passwordError = () => {
  notyf.error('  .   !');
};

const errorNotyf = status => {
  switch (status) {
    case 200:
      return notyf.success(' ,  !');
    case 201:
      return notyf.success(' ,  !');
    case 400:
    case 501:
      return notyf.error();
    case 401:
    case 403:
      return notyf.error(' !');
    case 404:
      return notyf.error('  ,    !');
    case 500:
    case 503:
      return notyf.error('  ,   !');
    default:
      break;
  }
};

export default { errorNotyf, passwordError, createPlanningWeek };
