import moment from 'moment';
import 'moment/locale/uk';
moment.locale('uk');

export const daysOfWeek = [
  { id: 1, largeName: '', shortName: '', pathname: 'monday' },
  { id: 2, largeName: '', shortName: '', pathname: 'tuesday' },
  { id: 3, largeName: '', shortName: '', pathname: 'wednesday' },
  { id: 4, largeName: '', shortName: '', pathname: 'thursday' },
  { id: 5, largeName: '', shortName: '', pathname: 'friday' },
  { id: 6, largeName: '', shortName: '', pathname: 'sarturday' },
  { id: 7, largeName: '', shortName: '', pathname: 'sunday' }
];

export const screenWidth = window.innerWidth;

export const prizesStyles = {
  flexDirection: 'row',
  width: '400px',
  backgroundColor: 'transparent',
  justifyContent: 'space-between',
  margin: '0 auto'
};
export const weekStart = moment().startOf('week');

export const weekRange = `${moment()
  .startOf('week')
  .format('DD MMMM')} - ${moment()
  .startOf('week')
  .add(6, 'days')
  .format('DD MMMM')}`;

export const currentWeekRange = `${moment()
  .startOf('week')
  .format('L')
  .substring(0, 5)} - ${moment()
  .startOf('week')
  .add(6, 'days')
  .format('L')}`;

export const nextWeekRange = `${moment()
  .startOf('week')
  .add(1, 'week')
  .format('L')
  .substring(0, 5)} - ${moment()
  .startOf('week')
  .add(1, 'week')
  .add(6, 'days')
  .format('L')}`;

export const getDay = num => {
  const data = moment()
    .startOf('week')
    .add(num - 1, 'days')
    .format('dddd, DD MMMM');
  if (data) return data;
};
